import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles, ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { amber, yellow } from '@material-ui/core/colors';
import { useHistory } from "react-router-dom";
import axios from "axios";
import Footer from "./Footer"


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.warning.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },

  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: theme.palette.warning.dark,
    color: "#fff",
    '&:hover': {
      backgroundColor: theme.palette.warning.main,
      color: "#fff",
  },
  signinText: {
    color: "#fff",
    
  }
  },
}));


const theme = createMuiTheme({
  palette: {
    primary: amber,
    secondary: yellow,
  },
});


export default function SignIn() {
  const classes = useStyles();
  const history = useHistory();

  const [user, setuser] = useState({
    username: "",
    password: ""
  });

  function handleChange (event) {
    const { name, value } = event.target;
  
      setuser((prevUser) => {
        return {
          ...prevUser,
          [name]: value,
        };
      });
  }

  function handleSubmit(event)  {
    event.preventDefault();
    
    axios.post("https://thisiskeeper.herokuapp.com/signin", user)
    .then(res => {
        history.push("/notes/"+user.username)
    })
    .catch(err => console.log(err));
    }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
        <ThemeProvider theme={theme}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            autoComplete="username"
            autoFocus
            color = "primary"
            onChange = {handleChange}
          /> 
          </ThemeProvider>
          <ThemeProvider theme={theme}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange = {handleChange}
          />
          </ThemeProvider>
          
          <FormControlLabel
            control={<Checkbox value="remember" color="default" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2" color="textSecondary">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="https://thisiskeeper.herokuapp.com/signup" variant="body2" color="textSecondary">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Footer />
      </Box>
    </Container>
  );
}
